import React,{ Component } from "react";
import logo from "./img/d.png";
import USDEUR from "./img/usdeuro.png";

class Header extends Component {
  render(){
  return (
    <div>
      <div className="row header">
        <div className="col-1 logo">
          <img src={logo} width="50px" />
        </div>
        <div className="col " align="right">
          <div className="forexMarket" style={{ float: "left" }} align="left">
            <div className="count">
              <img src={USDEUR} width="70px" />
            </div>
            <div className="44">
              <ul>
                <li>
                  <br />
                  <span></span>
                  <span> EUR / USD</span>
                </li>
                <li>
                  <span style={{ fontSize: "10px" }}>DIGITAL</span>
                </li>
              </ul>
            </div>
            <div className="count"></div>
          </div>
          <span style={{ paddingRight: "40px" }}>
            {" "}
            Wallet :{" "}
            <span style={{ color: "#595959" }}>
              <u> {this.props.account} </u>
            </span>{" "}
            <i class="far fa-copy"></i>
          </span>
          <label>
            <span>$ 10,000 </span>
            <span>REAL ACC </span>
          </label>
          <span style={{paddingRight:'25px'}}>
            {" "}
            <i className="fa fa-angle-down btn" aria-hidden="true"></i>{" "}
          </span>
          {/* <button className="btn btn-warning">MANAGE</button> */}
        </div>
      </div>
    </div>
  );
}
}
export default Header;