import React, { Component, useState, useEffect } from "react";
// import _downOrders from "./Orders/_greenOrders";
// import _redOrders from "./Orders/_redOrders";
// import _history from "./_history";
import ChartCurve from "./Apichart";
class Forex extends Component {
  render() {
    const addressorder = this.props.AddressOrders.sort(
      (a, b) => b.id - a.id
    ).map((order) => (
      //  const addressorder = this.props.AddressOrders.map((order) => (
      <ul key={order.id}>
        <li
          style={{
            color: order._bool ? "green" : "brown",
            fontWeight: "bold",
            display: "flex",
          }}
        >
          <div
            className="loader"
            style={{
              display: order.Fxid == this.props.forexcount ? "show" : "none",
            }}
          ></div>
          <div style={{ flex: 1, paddingLeft: "13px" }}>
            {order.id}

            <br />
            {order._bool ? "Up" : "Down"}
          </div>
        </li>
        <li>
          <span
            style={{
              color: order._bool ? "green" : "brown",
              fontWeight: "bold",
            }}
          >
            {(order._Amount / 1e18).toFixed(3)} BNB
          </span>
          <br />$ 30
        </li>
        <li>
          <span
            style={{
              color: order._bool ? "green" : "brown",
              fontWeight: "bold",
            }}
          >
            +0.38 BNB
          </span>
          <br />$ 27
        </li>
      </ul>
    ));

    const displaygreen = this.props.marketOrdersUp
      .sort((a, b) => b._Amount - a._Amount)
      .map((order) => (
        <ul className="slide-fade" align="right" key={order.id}>
          {/* {console.log('greencall',((order._Amount/this.props.totalPercentageUp) * 100).toFixed(2))} */}
          <li className="show" align="center">
            <span>{(order._Amount / 1e18).toFixed(3)} USD</span>
          </li>
          <li>{order.address}</li>
        </ul>
      ));
    // const displaygreen =  this.props.marketOrdersDown.sort((a, b) => (b._Amount - a._Amount)).map(( order) => (
    //   <ul align="right" key={order.id}>
    //   <li>
    //     <span>{order._Amount} usd</span>
    //   </li>
    //   <li>{order.address}</li>
    // </ul>
    // ))

    const displayred = this.props.marketOrdersDown
      .sort((a, b) => b._Amount - a._Amount)
      .map((order) => (
        <ul key={order.id}>
          <li>{order.address} -</li>
          <li align="center">
            <span>{(order._Amount / 1e18).toFixed(3)} USD</span>
          </li>
        </ul>
      ));
    return (
      <div>
        <div className="chart">
          <div className="chartCol" style={{ padding: 3 }}>
            <div id="chartdiv">
              <ChartCurve />
            </div>

            <div className="historydata">
              {/* <div className="reddata">
                <div className="_hddata">
                  DOWN
                  <br /> ORDERS
                </div>
                <div className="redOrdersCal">$ {this.props.downorder} </div>
                <div className="append">Total Orders</div>
                <div className="append apamt">Total Amount</div>
                <div className="redOrdersAdd">100</div>
              </div>
              <div className="greendata">
                <div className="_hddatagreen">
                  UP
                  <br /> ORDERS
                </div>
                <div className="greenOrdersCal">$ {this.props.uporder}</div>
                <div className="appendgreen">Total Orders</div>

                <div className="greenOrdersAdd">100</div>
                <div className="appendgreen gg">Total Amount</div>
              </div> */}
              <div className="row liveorders">
                <div className="col colgreen" id="style-4">
                  {displaygreen}
                </div>
                <div className="col colred" id="style-4">
                  {" "}
                  {displayred}
                </div>
              </div>
            </div>
          </div>
          <div className="colBuy">
            {/*                      Buy Tab                    */}
            <div>
              <div>
                <div className="enter">
                  <span>EXPIRES IN : </span>
                  <span id="timer" style={{ color: "#cc9a05", fontSize: "12px" }}>
                    
                    10 sec{" "}{" "}
                  </span>
                </div>
                <div className="enter2">
                  <span id="">LAST PRICE : </span>
                  <span>
                    {" "}
                    {/* <span className="_fis" id="currentprice">1.203</span>{" "} */}
                    <span className="_sec" id="currentprice">
                      867
                    </span>{" "}
                  </span>
                </div>
                <div className="enter2">
                  <span>INVEST : </span>
                  <input
                    type="number"
                    className="invest"
                    placeholder="100"
                    amount={this.props.amount}
                    onChange={this.props.handleChange}
                    // onChange={(e) => this.props.onChange(e.target.value)}
                  />
                  {/* <!--  <span> <span className="_fis">1.203</span> <span className="_sec">867</span> </span> --> */}
                </div>
                <div align="center">
                <u>
                   Profits (<span style={{color:'green'}}>+90%</span>) </u>
                  <br />$ <span id="percent"> 0</span> 
                </div>
                <div align="center">
                  <button
                    className="btn btn-success call"
                    onClick={(event) => {
                      event.preventDefault();
                      if (this.props.amount != 0) {
                        //   const amount = (this.props.amount);
                        this.props.placeOrder(true);
                      } else {
                        window.alert(
                          "Please type positive interger or float numbers"
                        );
                      }
                    }}
                  >
                    <i className="fas fa-external-link-square-alt" style={{paddingRight:'15px'}}></i> 
                    UP
                  </button>
                  <button
                    className="btn btn-danger call"
                    onClick={(event) => {
                      event.preventDefault();
                      if (this.props.amount != 0) {
                        //   const amount = (this.props.amount);
                        this.props.placeOrder(false);
                      } else {
                        window.alert(
                          "Please type positive interger or float numbers"
                        );
                      }
                    }}
                  >
                    <i className="fas fa-external-link-square-alt data2"></i>{" "}
                    DOWN
                  </button>
                </div>
              </div>
            </div>
            {/*                      Buy Tab                    */}

            <br />
            <br />
            <div align="center" style={{ fontSize: "13px" }}>
              History
            </div>
            <div>
              <div className="_hishd">
                <ul>
                  <li>Orders</li>
                  <li>Amount</li>
                  <li>Recieved</li>
                </ul>
              </div>
              <div className="_hishdstat" id="style-4">
                {addressorder}
              </div>
              {/* <_history /> */}
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Forex;
