import React, { Component } from "react";
import Header from "./_header";
import _chart from "./_chart";
import _footer from "./_footer";
import Modal from "./Modal";
import Web3 from "web3";
import {
  FOREX_ADDRESS,
  FOREX_ABI,
  MINING_ABI,
  FOREX_MINING,
} from "./Orders/Connect/config";
import { LabelBullet } from "@amcharts/amcharts4/charts";

const newArray = [];

const networkId = () =>
  newArray
    .sort((a, b) => b._Amount - a._Amount)
    .map((order) => (
      <ul align="right" key={order.id}>
        <li>
          <span>{order._Amount} usd</span>
        </li>
        <li>{order.address}</li>
      </ul>
    ));

class Forex extends Component {
  constructor(props) {
    super(props);
    this.state = {
      account: null,
      contractmine: null,
      contractmine_abi: null,
      contractmine_address: null,
      amount: null,
      balance: null,
      contract: null,
      event: null,
      loading: false,
      network: null,
      maxBet: 0,
      minBet: 0,
      web3: null,
      //---------------------------------------------
      uporder: 0,
      downorder: 0,
      totalOrders: 0,
      forexcount: 0,
      marketOrdersUp: [],
      marketOrdersDown: [],
      marketOrders: [],
      newOrder: 0,
      AddressOrders: [],
      percent: 0,
      totalPercentageUp: 0,
      contractSwitch: false,

      percUp: 0,
      currentTime: 0,
      percDown: 0,
      //---------------------------------------------
      wrongNetwork: false,
      contractAddress: null,
      newEvent: 0,
    };

    this.placeOrder = this.placeOrder.bind(this);
    this.setState = this.setState.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.readEvent = this.readEvent.bind(this);
    this.readEventForex = this.readEventForex.bind(this);
  }
  async readEvent() {
    this.state.contract.events.newOrder({}, async (error, event) => {
      const verdict = event.returnValues;
      console.log("verdict", verdict);
      this.setState({ newOrder: verdict });
      console.log(this.state.newOrder);
      this.updateOrder(
        event.returnValues.id,
        event.returnValues.index,
        event.returnValues.Player,
        event.returnValues.UPTRUEDOWNFALSE,
        event.returnValues.Amount,
        true
      );

      console.log("down", this.state.marketOrdersDown);
    });
  }

  async readEventForex() {
    this.state.contract.events.NewRound({}, async (error, event) => {
      console.log("newEvent", event);
      let fxRound = await this.state.contract.methods._ForexRound().call();
      this.setState({ forexcount: fxRound });
      this.setState({ marketOrdersDown: [] });
      this.setState({ marketOrdersUp: [] });
    });
  }

  cooldowntimer() {
    let godtimer_lastminute = 300;
    let i_godTimer = true;
    if (i_godTimer == true) {
      let _blocktime = new Date().getTime(); //current "blocktime" in milliseconds
      let _timer = 1615680911 - _blocktime / 1000;

      //   if (_timer > 0) {
      godtimer_lastminute = 0;
      let _hours = Math.floor(_timer / 3600);
      if (_hours < 10) {
        _hours = "0" + _hours;
      }
      let _minutes = Math.floor((_timer % 3600) / 60);
      if (_minutes < 10) {
        _minutes = "0" + _minutes;
      }
      let _seconds = parseFloat((_timer % 3600) % 60).toFixed(0);
      if (_seconds < 10) {
        _seconds = "0" + _seconds;
      }

      document.getElementById("timer").innerHTML =
        _minutes + " mins : " + _seconds + " sec ";
    }
  }

  async getorder() {
    let CheckMine = await this.state.contractmine.methods.pendingReward(this.state.account).call();
    console.log('CheckMine',CheckMine);
    let total = this.state.totalOrders;
    for (var i = 0; i <= total; i++) {
      let fx = await this.state.contract.methods
        .ORDERS(this.state.forexcount, i)
        .call();

      let id = fx.orderIndex;
      let _add = fx._User;
      let FxDX = fx._ForexIndex;
      let _bool = fx._TUPFDOWN;
      let _Amount = fx._Amount;

      this.updateOrder(id, FxDX, _add, _bool, _Amount, false);
      console.log("up", this.state.marketOrdersUp);
      console.log("down", this.state.marketOrdersDown);
      console.log("total", this.state.totalPercentageUp);
    }
  }

  updateOrder(id, fxdx, address, _bool, _amount, checkDown) {
    const marketOrdersUp = [...this.state.marketOrdersUp];
    const marketOrdersDown = [...this.state.marketOrdersDown];
    const AddressOrders = [...this.state.AddressOrders];

    marketOrdersUp.push({
      id: id,
      address: address,
      _bool: _bool,
      _Amount: _amount,
    });
    marketOrdersDown.push({
      id: id,
      address: address,
      _bool: _bool,
      _Amount: _amount,
    });
    AddressOrders.push({
      id: this.state.AddressOrders.length,
      Fxid: fxdx,
      address: address,
      _bool: _bool,
      _Amount: _amount,
    });

    if (checkDown) {
      this.setState({ AddressOrders: AddressOrders });
    }

    if (_bool) {
      this.setState({ marketOrdersUp: marketOrdersUp });
    } else {
      this.setState({ marketOrdersDown: marketOrdersDown });
    }
  }

  async playerHistory() {
    let orderCount = await this.state.contract.methods
      .AddOrderLimit(this.state.account)
      .call();
    console.log("nnnnnn", orderCount);
    for (var i = 0; i <= orderCount; i++) {
      //  console.log('iii' + a ,i);
      let fx = await this.state.contract.methods
        .addressOrders(this.state.account, i)
        .call();

      let id = this.state.AddressOrders.length;
      let Fxid = fx._ForexIndex;
      let _add = fx._User;
      let _bool = fx._TUPFDOWN;
      let _Amount = fx._Amount;
      const AddressOrders = [...this.state.AddressOrders];
      AddressOrders.push({
        id: id,
        Fxid: Fxid,
        address: _add,
        _bool: _bool,
        _Amount: _Amount,
      });
      if ((_add = this.state.account)) {
        this.setState({ AddressOrders: AddressOrders });
      }

      //   this.updateOrder(id, _add, _bool, _Amount);
      console.log("up", this.state.marketOrdersUp);
      console.log("down", this.state.marketOrdersDown);
      console.log("total", this.state.totalPercentageUp);
      //  }
    }
  }

  async componentWillMount() {
    setInterval(this.cooldowntimer, 1000);
    await this.loadWeb3();
  }

  /** !UPDATE **/
  async loadWeb3() {
    if (typeof window.ethereum !== "undefined" && !this.state.wrongNetwork) {
      let accounts,
        network,
        balance,
        web3,
        contract,
        contract_abi,
        contract_address;

      // console.log(window.ethereum.networkVersion, 'window.ethereum.networkVersion');  --- check current network

      //don't refresh DApp when user change the network
      window.ethereum.autoRefreshOnNetworkChange = false;

      web3 = new Web3(window.ethereum);
      this.setState({ web3: web3 });
      //------------------------------------------------------------------

      contract_abi = FOREX_ABI;
      contract_address = FOREX_ADDRESS; //rinkeby
      contract = new web3.eth.Contract(contract_abi, contract_address);
      let _ContMine;
      _ContMine = new web3.eth.Contract(MINING_ABI, FOREX_MINING);

      accounts = await web3.eth.getAccounts();
      
      //Update the data when user initially connect
      if (typeof accounts[0] !== "undefined" && accounts[0] !== null) {
        balance = await web3.eth.getBalance(accounts[0]);

        this.setState({ account: accounts[0], balance: balance });
        console.log("Acoounts", accounts[0]);
      }

      this.setState({
        contract: contract,
        contractAddress: contract_address,
        contractmine: _ContMine,
        contractmine_address: FOREX_MINING,
      });

      //--------------------------------------------------------------------------------------------------
      let order = await this.state.contract.methods.check().call();
     

      this.setState({ uporder: order[0] });
      this.setState({ downorder: order[1] });
      this.setState({ totalOrders: order[2] });
      let fx = await this.state.contract.methods._ForexRound().call();
      this.setState({ forexcount: fx });
      //--------------------------------------------------------------------------------------------------

      //Update account&balance when user change the account
      window.ethereum.on("accountsChanged", async (accounts) => {
        if (typeof accounts[0] !== "undefined" && accounts[0] !== null) {
          balance = await web3.eth.getBalance(accounts[0]);

          this.setState({ account: accounts[0], balance: balance });
        } else {
          this.setState({ account: null, balance: 0 });
        }
      });

      // Network id 56  -- main
      // Network id 97  --- test

      //Update data when user switch the network
      window.ethereum.on("chainChanged", async (chainId) => {
        network = parseInt(chainId, 16);
        console.log("Network id", network);
        if (network !== 4) {
          this.setState({ wrongNetwork: true });
        } else {
          if (this.state.account) {
            balance = await this.state.web3.eth.getBalance(this.state.account);
            // maxBet = await this.state.web3.eth.getBalance(this.state.contractAddress)
            // minBet = await this.state.contract.methods.weiInUsd().call()

            // this.setState({ balance: balance, maxBet: maxBet, minBet: minBet })
            this.setState({ balance: balance });
          }
          this.setState({
            network: network,
            loading: false,
            onlyNetwork: false,
            wrongNetwork: false,
          });
        }
      });
    }
    this.readEvent();
    this.readEventForex();
    this.getorder();
    this.playerHistory();
    // this.addressOrders();
  }

  // onChange(value) {
  //   this.setState({ amount: value });
  // }
  handleChange(event) {
    let perc = (Number(event.target.value * 1e18) * Number(190)) / 100;
    this.setState({ amount: event.target.value });
    document.getElementById("percent").textContent = (perc / 1e18).toFixed(4);

    console.log("perc", perc / 1e18);
    // this.setState({ percent: perc/1e18 });
  }
  async placeOrder(data) {
    const amount = this.state.web3.utils.toWei(this.state.amount);
    console.log(amount);
    const networkId = await this.state.web3.eth.net.getId();
    if (networkId !== 97) {
      this.setState({ wrongNetwork: true });
    } else if (
      typeof this.state.account !== "undefined" &&
      this.state.account !== null
    ) {
      this.state.contract.methods
        .placeOrder(data, amount)
        .send({ from: this.state.account })
        .on("transactionHash", (hash) => {
          this.setState({ loading: true });
          // window.alert(hash)
        })
        .on("error", (error) => {
          //  window.alert('Error')
        });
    } else {
      window.alert("Problem with account or network");
    }
  }

  render() {
    return (
      <div className="main">
        <Modal />
        <Header account={this.state.account} balance={this.state.balance} />
        <_chart
          percent={this.state.percent}
          amount={this.state.amount}
          forexcount={this.state.forexcount}
          handleChange={this.handleChange}
          placeOrder={this.placeOrder}
          marketOrdersUp={this.state.marketOrdersUp}
          marketOrdersDown={this.state.marketOrdersDown}
          AddressOrders={this.state.AddressOrders}
          uporder={this.state.uporder}
          downorder={this.state.downorder}
          web3={this.state.web3}
        />
        <_footer />
      </div>
    );
  }
}

export default Forex;
